@font-face {
  font-family: "DIGIT";
  src: url('../../../src/assets/fonts/DS-DIGIT.TTF');
}

.body {
  align-items: center;
  background: url('../../../src/assets/images/bg.png') no-repeat center;
  background-size: 100% auto;
  box-sizing: border-box;
  color: #ff0000;
  display: flex;
  flex-direction: column;
  font-family: 'Slackey', sans-serif;
  justify-content: center;
  margin: 0;
  height: 100vh;
  padding: 30px;
  text-align: center;
  position: relative;

  .shizhong-box {
    position: absolute;
    right: 130px;
    top: 130px;
    width: 210px;
    height: 210px;
    padding: 10px 10px;
    backdrop-filter: blur(10px);
    background-color: rgba(0, 0, 0, 0.185);
    box-shadow: rgba(0, 0, 0, 0.3) 2px 8px 8px;
    border-left: 2px rgba(254, 254, 254, 0.4) solid;
    border-right: 2px rgba(102, 102, 102, 0.3) solid;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
  }

  .button-bottom {
    position: absolute;
    bottom: 100px;
    padding: 5px 20px;
    background-color: #ff007f10;
    border-radius: 8px;
    color: #ffffff50;
    font-size: 20px;
  }

  .box {
    width: fit-content;
    padding: 10px 10px;
    backdrop-filter: blur(4px);
    background-color: rgba(0, 0, 0, 0.075);
    box-shadow: rgba(0, 0, 0, 0.3) 2px 8px 8px;
    border-top: 2px rgba(254, 254, 254, 0.4) solid;
    border-bottom: 2px rgba(102, 102, 102, 0.3) solid;
    border-radius: 8px;
    height: fit-content;
    transition-duration: .2s;
    transition-property: all;
    // margin-top: -180px;
  }

  .tbs {
    font-size: 32px;
    font-weight: bold;
  }

  .div {
    line-height: 1;
  }

  .div > span {
    display: inline-block;
    overflow: hidden;
    transition-duration: 0.2s;
    transition-property: transform;
    margin: 25px 0;
  }

  .div > span span {
    display: inline-block;
    transition-duration: 0.5s;
    transition-property: transform;
    transition-timing-function: cubic-bezier(0.34, 1.56, 0.64, 1);
    font-family: DIGIT;
    color: #55ffff;
    font-size: 60px;
    width: 1em;
    margin-left: 10px;
  }

  .div > span:nth-child(odd) {
    transform: translateY(50%);
  }

  .div > span:nth-child(odd) span {
    transform: translateY(-100%);
  }

  .div > span:nth-child(even) {
    transform: translateY(-50%);
  }

  .div > span:nth-child(even) span {
    transform: translateY(100%);
  }

  .div.loaded > span {
    transform: translateY(0);
  }

  .div.loaded > span span {
    transform: translateY(0);
  }
}


/* 
		控制按钮
	 */
.heart-container {
  --heart-color: rgb(255, 91, 137);
  position: relative;
  width: 50px;
  height: 50px;
  transition: .3s;
}

.heart-container .checkbox {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 20;
  cursor: pointer;
}

.heart-container .svg-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.heart-container .svg-outline,
.heart-container .svg-filled {
  fill: var(--heart-color);
  position: absolute;
}

.heart-container .svg-filled {
  animation: keyframes-svg-filled 1s;
  display: none;
}

.heart-container .svg-celebrate {
  position: absolute;
  animation: keyframes-svg-celebrate .5s;
  animation-fill-mode: forwards;
  display: none;
  stroke: var(--heart-color);
  fill: var(--heart-color);
  stroke-width: 2px;
}

.z-label .svg-container .svg-filled {
  display: block
}

.z-label .svg-container .svg-celebrate {
  display: block
}

@keyframes keyframes-svg-filled {
  0% {
    transform: scale(0);
  }

  25% {
    transform: scale(1.2);
  }

  50% {
    transform: scale(1);
    filter: brightness(1.5);
  }
}

@keyframes keyframes-svg-celebrate {
  0% {
    transform: scale(0);
  }

  50% {
    opacity: 1;
    filter: brightness(1.5);
  }

  100% {
    transform: scale(1.4);
    opacity: 0;
    display: none;
  }
}


.aixing {
  --aixing-size: 20px;
  width: var(--aixing-size);
  background-color: pink;
  height: var(--aixing-size);
  transform: rotate(-45deg);
  // box-shadow: 0 4px 0 0px red;
}

.aixing:before {
  position: absolute;
  content: "";
  background-color: pink;
  width: var(--aixing-size);
  height: var(--aixing-size);
  top: calc(var(--aixing-size) / -2);
  left: 0px;
  border-radius: 50px;
  // box-shadow: 0 4px 0 0px red;
}

.aixing:after {
  position: absolute;
  content: "";
  background-color: pink;
  width: var(--aixing-size);
  height: var(--aixing-size);
  right: calc(var(--aixing-size) / -2);
  border-radius: 50px;
  // box-shadow: 0 4px 0 0px red;
}