.dong {
  opacity: 1;
  animation: dong 1s linear infinite;
}

.nodong {
  opacity: 1;
}

@keyframes dong {
  0% {
    opacity: 1;
  }

  20% {
    opacity: 1;
  }

  30% {
    opacity: 0;
  }

  70% {
    opacity: 0;
  }

  80% {
    opacity: 1;
  }

  100% {
    opacity: 1;
  }
}